html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__link {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  flex-direction: row;
  margin: 0;
  padding: 0;
  display: flex;
}

li {
  list-style-type: none;
}

:root {
  --color: #00acdc;
}

.nav__item {
  margin-left: 20px;
  display: block;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  text-align: center;
  margin: 0;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--color);
}

.is-active {
  position: relative;
}

.is-active:after {
  background: var(--color);
  content: "";
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

